import React, { useEffect, useRef } from "react";
import styled from "styled-components";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
// import { Row, Col } from "react-bootstrap";
import { iphoneimages } from "../pages/ImageSequence1";
// import { SlowMo } from "gsap/EasePack";

gsap.registerPlugin(ScrollTrigger);
// gsap.registerPlugin(SlowMo);

const StyledVideoSequenceSection = styled.div`
  // background: #000;
  width: 100vw;
  position: relative;
  
  .videosequence__wrapper {
    max-width: 90vw;
    margin: 0 auto;
  }
  .videosequence__container {
    overflow-y:hidden;
    position: relative;
    height: 100vh;
    // width: 100vw;
    background-color:white;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #080f0f;
    .videosequence__text {
      flex: 0 0 0%;
      max-width: 50%;
      h1 {
        font-size: 96px;
        line-height: 0.875;
        font-weight: 700;
        letter-spacing: 0.009em;
        color: #ffffff;
      }
      h3 {
        padding-top: 10px;
        font-size: 26px;
        line-height: 1.15385;
        font-weight: 700;
        letter-spacing: 0.002em;
        margin-top: 8px;
        color: #ffffff;
      }
      a.videosequence__button {
        display: inline-block;
        padding: 1em 2em;
        margin: 7em 0.5em 0.5em 0;
        border-radius: 2em;
        text-decoration: none;
        font-weight: 400;
        color: #080f0f;
        background-color: #00efeb;
        text-align: center;
        transition: all 0.6s;
        &:hover {
          background-color: #00bcb9;
        }
      }
    }
  }
`;

function VideoSequenceSection() {
  const videoSequenceTriggerRef = useRef();
  const canvasRef = useRef();
  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");

    canvas.width =1920;
    canvas.height = 1080;

    const frameCount = 64;
    const currentFrame = (index) => {
      return iphoneimages[index];
      // console.log(iphoneimages[0]);
    };

    const images = [];

    const iphone = {
      frame: 0,
    };

    for (let i = 0; i < frameCount; i++) {
      const img = new Image();
      img.src = currentFrame(i);
      images.push(img);
      // console.log(currentFrame(0));
      // console.log(img);
    }
    // console.log(videoSequenceTriggerRef.current.offsetHeight);
    gsap.to(iphone, {
      // defaults: { ease: "power1" },
      frame: frameCount - 1,
      snap: "frame",
      scrollTrigger: {
        trigger: videoSequenceTriggerRef.current,
        start: "top center",
        // end: () => "+=" + videoSequenceTriggerRef.current.offsetHeight,
        end: ()=> "+=400",
        scrub: true,
        // pin: true,
        // anticipatePin: 1,
        // markers: true,
      },
      onUpdate: updateImage,// use animation onUpdate instead of scrollTrigger's onUpdate
    });

    images[0].onload = updateImage;

    function updateImage() {
      context.clearRect(0, 0, canvas.width, canvas.height);
      context.drawImage(images[iphone.frame], 0, 0);
    }
  }, []);

  return (
    <StyledVideoSequenceSection ref={videoSequenceTriggerRef}>
      <div className="videosequence__wrapper">
        <div className="videosequence__container ">
          {/* <div className="videosequence__text "  id="title_vitext">
            
            <h1>SEWELO</h1>
            <h3>
              Indulge in the art of fragrance  
              <br />
              and elevate your senses.
            </h3>
            <Link to="" className="videosequence__button">
              Download
            </Link>
          </div> */}
          <div className="videosequence__image">
            <canvas className="canvas1" ref={canvasRef} />
          </div>
        </div>
      </div>
    </StyledVideoSequenceSection>
  );
}
export default VideoSequenceSection;
